.story {
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 120px;
  height: 200px;
  box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
  margin: 0px 5px;
  transition: all 0.2 linear 0s;
  border-radius: 10px;
  cursor: pointer;
}

.story:hover {
  transform: scale(1.07);
}
.story .MuiAvatar-root {
  margin: 10px;
  border: 2px solid #2e81f4;
}
.story h4 {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: #fff;
  font-size: 13px;
  font-weight: bold;
}
